const customerTableHead = [
  "",
  "name",
  "email",
  "phone",
  "total orders",
  "total spend",
  "location",
];

const renderHead: TRenderHeadAction = (item: string, index: number) => (
  <th key={index}>{item}</th>
);

const renderBody: TRenderBodyAction<ICustomer> = (
  item: ICustomer,
  index: number,
  first?: ReactNode,
  end?: ReactNode
) => (
  <tr key={index}>
    {first}
    <td>{item.id}</td>
    <td>{item.name}</td>
    <td>{item.email}</td>
    <td>{item.phone}</td>
    <td>{item.totalOrders}</td>
    <td>{item.totalSpend}</td>
    <td>{item.location}</td>
    {end}
  </tr>
);

const CustomersPage = () => {
  const [page, setPage] = useState(0);

  const { data } = carQueries.useCarQuery({
    pageSize: 50,
    pageNum: page,
  });

  const handleCustomerDelete = async (id: number) => {
    try {
      await carApi.removeCar(id);
    } catch (error) {
      console.log(error);
    }
  };

  const CustomerPageTable = PageTable<ICustomer>({
    title: "customers",
    pagesCount: data?.totalPages || 0,
    currPage: page,
    headData: customerTableHead,
    bodyData: customerList,
    renderHead: renderHead,
    renderBody: renderBody,
    setPage: setPage,
    form: CustomerForm,
    handleDelete: handleCustomerDelete,
  });

  return CustomerPageTable;
};

export default CustomersPage;
